export class Song {
    constructor({id, mid, singer, name, album, during, image, url}) {
      this.id = id;
      this.mid = mid;
      this.singer = singer;
      this.name = name;
      this.album = album;
      this.during = during;
      this.image = image;
      this.url = url;
    }
  };

  
  export function createSong(musicData) {
    return new Song({
        id: musicData.songid,
        mid: musicData.songmid,
        singer: _normalizeSinger(musicData.singer),
        name: musicData.songname,
        album: musicData.albumname,
        during: musicData.interval,
        image: `https://y.gtimg.cn/music/photo_new/T002R300x300M000${musicData.albummid}.jpg?max_age=2592000`,
        url: `http://ws.stream.qqmusic.qq.com/${musicData.songid}.m4a?fromtag=46`
    });
};

function _normalizeSinger(singer){
    var singers=[];
    if(singer && Array.isArray(singer)){
        singer.forEach(item => {
            singers.push(item.name);
        });
      return  singers.join("/");
    }
    return "";
}




